<template>
  <section class="visual">    
    <!-- Swiper -->
    <Swiper
      :modules="modules"
      :autoplay ="{
        delay: 1000,
        disableOnInteraction: false
      }"
      :loop = "true"
      :pagination = "{
        el: '.sw-visual-pg',
        clickable: 'true'
      }"
      :effect="'fade'"
      v-on:swiper="visuaSlide"
      class="sw-visual"
    >
      <SwiperSlide class="swiper-slide" v-for="(item, index) in visualData" :key="index">
        <a 
          :href="item.link" 
          :style="{
            backgroundImage:`url(${ resW ? item.imgurl : item.imgurl_mb })`,
            backgroundSize: 'cover',
            backgroundPosition : 'center'
          }"
          :data-bg-mb="item.imgurl_mb"
          :data-bg = "item.imgurl"
        >
        </a>
      </SwiperSlide>      

      <!-- 슬라이더 페이지네이션 -->
      <div class="sw-visual-control">
        <div class="sw-visual-pg"></div>
        <button class="sw-visual-bt">
          <!-- 슬라이더를 클릭했을 때 play / start 를 하고 싶다. -->
          <span class="material-icons-outlined" @click="controlSlide">
            <!-- 클릭을 했을 때에 아이콘 모양을 바꾸기 위해서 작성 -->
            <!-- 구글 아이콘인 경우만 해당 -->
            {{slideState}}
          </span>
        </button>
      </div>
      
    </Swiper>

  </section>
</template>

<script>
  import {Swiper,SwiperSlide} from 'swiper/vue'  
  import 'swiper/css'
  import {Autoplay, EffectFade, Pagination} from 'swiper'
  import 'swiper/css/pagination'
  import 'swiper/css/effect-fade'
  import {computed, ref, onMounted} from 'vue'
  import {useStore} from 'vuex'
  export default {
    components: {
      Swiper,
      SwiperSlide
    },

    setup() {
      // 현재 1400보다 크면 true, 작으면 false
      const resW = ref(true);

      onMounted( () => {
        const checkWin = () => {
          let w = window.innerWidth;
          if(w <= 1400) {
            resW.value = false;            
          }else{
            resW.value = true;            
          }
        }
        window.addEventListener('resize', checkWin);
        checkWin()
      })
      // state의 visualData 를 받아온다.
      const store = useStore();
      // getter 의 visualData computed 로 받아온다.
      const visualData = computed( () => store.getters.visualData );
      // Style 바인딩을 이용한다. v-bind:style="내용"
      // Swipr 슬라이드를 저장하려는 용도
      const slide = ref(null)
      // Swiper 슬라이드 객체를 저장하기 위해서
      const visuaSlide = (swiper) => {
        // ref 라서 .vaule 저장함.
        slide.value = swiper;
        slide.value.slideTo(1)
      }

      // 아이콘을 변경하기 위한 문자열 저장
      const slideState = ref('pause')

      // 버튼 아이콘을 클릭할 때마다 실행
      const controlSlide = () => {
        // 상태 파악후 start / pause / 아이콘 모양 변경
        if(slide.value.autoplay.running) {
          slide.value.autoplay.stop();
          slideState.value = 'play_arrow'
        }else{
          slide.value.autoplay.start();
          slideState.value = 'pause'
        }
      }
      return {
        modules: [Autoplay, EffectFade, Pagination],
        // Swiper 를 template 에 사용했으므로 전달해 준다.
        visuaSlide,
        controlSlide,
        slideState,
        visualData,
        resW
      }
    }

  }
</script>

<style>
.visual {
  position: relative;
  display: block;
  height: 477px;
}

.sw-visual {
  width: 100%;
  height: 100%;
}

.sw-visual a {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

.vs-1 {
  background: url('@/assets/images/banner_1_20200915_0.jpg') no-repeat center;
  background-size: cover;
}

.vs-2 {
  background: url('@/assets/images/banner_2_20200915_0.jpg') no-repeat center;
  background-size: cover;
}


.vs-3 {
  background: url('@/assets/images/banner_4_20200915_0.jpg') no-repeat center;
  background-size: cover;
}

.sw-visual-pg {
  display: inline-block;
  width: auto !important;
}

.sw-visual-pg .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  margin-right: 10px;
  background-color: #fff;
  opacity: 0.5;
}

.sw-visual-pg .swiper-pagination-bullet-active {
  width: 12px;
  height: 12px;
  margin-right: 10px;
  background-color: #fff;
  opacity: 1;
}

.sw-visual-bt {
  position: relative;
  display: inline-block;
  width: 23px;
  height: 22px;
  vertical-align: bottom;
  background: url('@/assets/images/stop.png') no-repeat center;
  background: transparent;
  cursor: pointer;
  border: 0;

}

.sw-visual-control {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  z-index: 9999;
  display: block;
}

.sw-visual-bt-play {
  background: url('@/assets/images/play.png') no-repeat center;
}

.material-icons-outlined {
  color: #fff;
  font-size: 24px;
}
/* visual PC 버전 */
  /* visual 반응형 버전 */
  @media all and (max-width: 1400px) {
    .visual {
      height: 50vw;
    }
  }
</style>